header {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 1) 0%,
        rgba(0, 0, 0, 0) 100%
      ),
      url("../Images/BG_Fon.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  .header-navigation {
    width: 100%;
    height: 80px;
    box-sizing: border-box;
    /* padding-left: 170px;
    padding-right: 170px; */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .logo_block {
    max-width: 310px;
    width: 210px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .logo_block img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  nav {
    max-width: 720px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .nav-menu {
    width: 100%;
    height: 100%;
    display: flex;
    padding: 0;
    align-items: center;
    justify-content: flex-end;
  }
  .nav-menu-item {
    font: normal normal 18px "SF Pro Light", sans-serif;
    width: 100px;
    height: 40px;
    margin: 0 20px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s all;
    color: #fff;
    position: relative;
    overflow: hidden;
  }
  
  .nav-menu-item span {
    position: absolute;
    transition: 0.5s all;
    background: #fff;
  }
  
  span.top,
  span.bottom {
    width: 100%;
    height: 2px;
  }
  span.left,
  span.right {
    width: 2px;
    height: 100%;
  }
  
  span.top {
    top: 0;
    left: -100%;
  }
  
  span.left {
    top: -100%;
    right: 0;
  }
  
  span.bottom {
    bottom: 0;
    right: -100%;
  }
  
  span.right {
    bottom: -100%;
    left: 0;
  }
  
  .nav-menu-item:hover .top {
    left: 0;
  }
  
  .nav-menu-item:hover .left {
    top: 0;
  }
  
  .nav-menu-item:hover .bottom {
    right: 0;
  }
  
  .nav-menu-item:hover .right {
    bottom: 0;
  }
  
  .header-text-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: #fff;
    height: 100%;
  }
  .text-wrapper .glg-title {
    color: #339a00;
    font-size: 84px;
    font-family: sans-serif;
    margin: 0;
    text-decoration: underline;
  }
  .text-wrapper .glg-desc {
    color: #fff;
    font-size: 22px;
    max-width: 480px;
    font-weight: 100;
    font-family: "SF Pro Light", sans-serif;
    min-width: 280px;
    letter-spacing: 1px;
  }