main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 100px;
  }
  .title{
    color: #fff;
    font-family: "SF Pro Regular", sans-serif;
    font-size: 36px;
    letter-spacing: 3px;
    text-transform: uppercase;
    position: relative;
  }
  
  .title::after, .title::before{
    content: "";
    height: 1px;
    width: 100%;
    background-color: #339a00;
    border: 1px solid #339a00;
    position: absolute;
    top: 50%;
  }
  .title::before{
    left: -110%;
  }
  .title::after{
    right: -110%;
  }
  .content-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }


  @media (max-width: 768px){
    .title::after, .title::before{
      display: none;
      
    }
  }