footer{
    background-color: #339a00;
    width: 100%;
    padding: 25px 0;
  }
  .footer-ctn{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
  }
  .footer-contacts{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
  .contacts-title{
    color: #fff;
    font-family: "SF Pro Regular", sans-serif;
    font-size: 28px;
    letter-spacing: 4px;
    text-transform: uppercase;
  }
  .contact-item{
    color: #fff;
    font-family: "SF Pro Regular", sans-serif;
    font-size: 15px;
    line-height: 28px;
    letter-spacing: 4px;
  }