
* {
  user-select: none;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

html, body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  background-color: #000;
}
a {
  color: inherit;
  text-decoration: none;
}

ul{
  list-style: none;
  padding: 0;
  margin: 0;
}