.employee-block{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border: 1px solid #fff;
    border-radius: 10px;
    max-width: 768px;
    min-height: 236px;
    /* height: 240px; */
    color: #fff;
    margin: 10px 0;
  }
  
  .employee-block:nth-child(odd){
    flex-direction: row;
  }
  .employee-block:nth-child(even){
    flex-direction: row-reverse;
  }
  .employee-block:nth-child(even) .employee-img img{
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
  }
  
  .employee-img{
    width: 30%;
    height: 100%;
    min-width: 240px;
    display: flex;
  }
  .employee-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
  }
  .employee-text{
    width: 70%;
    box-sizing: border-box;
    padding: 0 15px;
    height: 100%;
    min-width: 240px;
    max-width: 525px;
    display: flex;
    flex-direction: column;
  }
  h3.employee-name {
    font-size: 24px;
    font-family: "SF Pro Regular", sans-serif;
    letter-spacing: 2px;
  }
  p.employee-desc {
    font-size: 18px;
    font-family: "SF Pro Regular", sans-serif;
    letter-spacing: 0.02rem;
    line-height: 23px;
  }


  @media (max-width: 768px){
    .employee-block:nth-child(odd){
      flex-direction: row;
    }
    .employee-block:nth-child(even){
      flex-direction: row;
    }
    .employee-img{
      width: 100%;
      height: 100%;
      min-width: 240px;
      display: flex;
    }
  
    .employee-text{
      width: 100%;
    }
  }